  --background: #ffffff;
  --foreground: #000;
  --color-selected-black: #00000099;
  --hero-background: #ffffff;
  --hero-foreground: #000;
  --dark-grey: #404040;
  --selected-color: #E3ECFF;
  --selected-color-dark: #555b69;
  --selected-bottom-dark: #95B6FF;
  --selected-bg-bottom-dark: #314779;
  --input-color: #B3B3B3;
  --font-size-xxs: 13px;
  --space-30: 120px;
  --space-7_5: 30px;
  --space-92_5: 23.125rem;
  --screen-xxs: 360px;
  --screen-xs: 425px;
  --screen-xss: 525px;
  --screen-400: 400px;
  --screen-sm: 640px;
  --screen-md: 768px;
